.body {
    display: flex;
    flex-flow: row wrap;
}

.card {
    flex: 1 1 46%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 20px;
    margin: 15px;
    box-shadow: 0px 2px 4px 0px rgb(167, 167, 167);
    display: flex;
    flex-direction: column;
}
.card a {
    flex: 1 1 auto;
    text-decoration: none;
    color: black;
}
.title {
    font-weight: bold;
    display: block;
    margin: 0 0 15px 0;
}
.poster {
    /*flex: 1 1 auto;*/
}
.poster img {
    width: 100%;
    height: auto;
}
.info {
    flex: 0 1 auto;
    color: rgb(145, 145, 145);
}
.content {
    font-size: 14px;
}